import { useContext } from 'react'
import last from 'lodash/last'

import MediaContext from './mediaContext'
import { responsiveIndex } from '../../components/ThemeProvider/theme';

export default () => {
  const { responsive, containerWidth, em, resizeRatio } = useContext(MediaContext)
  return {
    isMobile: !responsive[responsiveIndex[0] - 1],
    isLaptop: !responsive[responsiveIndex[1] - 1],
    containerWidth,
    em,
    resizeRatio,
    getCurrentValue: (resArr) => {
      const ff = responsive.findIndex(v => !v)
      return ff === -1 ? last(resArr) : resArr[ff - 1]
    },
  }
}
