import React, { useState, useEffect } from 'react';
import Measure from 'react-measure'
import { SizeMe } from 'react-sizeme'
import { TiArrowSortedDown } from 'react-icons/ti'
import loSize from 'lodash/size'

import Link from '../../../components/Link'
import Circle from '../../../components/Circle'
import Box from '../../../components/Box'
import Text from '../../../components/Text'
import NumberAndText from '../../../components/NumberAndText'
import Image from '../../../components/Image'
import SVG from '../../../components/SVG'
import DotsBg from '../../../components/DotsBg'
// import browser from '../../../components/utils/browser'

import FullLeft from '../../FullLeft'
import FullRight from '../../FullRight'

import left from './left.svg'
import right from './right.svg'
import logo from './logo.svg'

const duration = 1500

// const isiOS = browser.getOSName() === 'iOS'
// const isMobile = browser.getPlatformType !== 'desktop'

const Page = ({ fullpageApi, resizeRatio, inited }) => {
  const [leftPos, setLeftPos] = useState({})
  const [rightPos, setRightPos] = useState({})
  const [animated, setAnimated] = useState(false)
  const [doInited, setDoInted] = useState()
  useEffect(() => {
    if (inited) {
      setTimeout(() => {
        setDoInted(true)
        setTimeout(() => setAnimated(true), duration * 1.2)
      })
    }
  }, [inited])

  return (
    <DotsBg height="100%" position="relative">
      <SizeMe>
        {({ size }) => (
          <Box position="absolute" width="100%" height="100%">
            <SVG width="100%" height="100%">
              {loSize(leftPos) && (
                <polygon
                  fill="#fcc013"
                  opacity={animated ? 0.8 : 0}
                  points={[
                    [271 * resizeRatio, 290 * resizeRatio],
                    [289 * resizeRatio, 318 * resizeRatio],
                    [leftPos.left + leftPos.width / 2, leftPos.bottom],
                    [leftPos.left + leftPos.width / 4, leftPos.bottom - leftPos.height * 0.93],
                  ].map(d => d.join()).join(' ')}
                />
              )}
              {loSize(rightPos) && (
                <polygon
                  fill="#fcc013"
                  opacity={animated ? 0.8 : 0}
                  points={[
                    [size.width - (1366 - 1095) * resizeRatio, 290 * resizeRatio],
                    [size.width - (1366 - 1079) * resizeRatio, 318 * resizeRatio],
                    [rightPos.left + rightPos.width / 2, rightPos.bottom],
                    [rightPos.right - rightPos.width / 4, rightPos.bottom - rightPos.height * 0.93],
                  ].map(d => d.join()).join(' ')}
                />
              )}
            </SVG>
          </Box>
        )}
      </SizeMe>
      <FullLeft transition={`transform ${duration}ms`} transform={`translateX(${doInited ? 0 : -100}%)`} src={left} />
      <FullRight transition={`transform ${duration}ms`} transform={`translateX(${doInited ? 0 : 100}%)`} src={right} />

      <Box position="absolute" top="5.375em" left="50%" transform="translateX(-50%)" opacity={+animated}>
        <Measure bounds onResize={contentRect => setLeftPos(contentRect.bounds)}>
          {({ measureRef }) => <Circle ref={measureRef} width="16.375em" bg="primary" />}
        </Measure>
        <Measure bounds onResize={contentRect => setRightPos(contentRect.bounds)}>
          {({ measureRef }) => <Circle ref={measureRef} width="16.375em" bg="primary" ml="-4.5em" />}
        </Measure>
      </Box>
      <Box pt="10em" textAlign="center" position="relative">
        <Box opacity={+animated}>
          <Text.h1>數據再登場</Text.h1>
          <Text.h3>兩廳院售票消費行為年度報告</Text.h3>
        </Box>
        <Box color="white" mt="7em" opacity={+animated} transition={`opacity ${duration}ms ease-in-out`}>
          <Text.h5 mb="1em">
          作為國內代表性的藝文票券銷售平台「兩廳院售票系統」，<br />
          期許成為藝文觀眾與場館、團隊的橋梁，<br />
          <NumberAndText lineHeight="1" is="span">今年再度呈現2013-2019年表演藝術大數據，</NumberAndText><br />
          以拓展數據共享的價値
          </Text.h5>
          <Text>本次委託「國立交通大學統計學研究所」分析，<br />
          展現國內藝文消費的樣貌及產業發展趨勢，回饋表演藝術產業多元的行銷應用參考。</Text>
        </Box>
      </Box>
      <Box
        pt="3em"
        opacity={+animated}
        transition={`opacity ${duration}ms ease-in-out ${duration / 2}ms`}
        textAlign="center"
      >
        <Circle
          width="2.75em"
          bg="primary"
          onClick={() => {
            fullpageApi.moveSectionDown()
          }}
        >
          <TiArrowSortedDown size="1.55em" />
        </Circle>
      </Box>
      <Box left="1.5em" top="1.5em" width="8.5em" position="absolute">
        <Link href="https://www.artsticket.com.tw/CKSCC2005/home/home00/index.aspx">
          <Image src={logo} />
        </Link>
      </Box>
    </DotsBg>
  );
};

export default Page;
